.content {
    position: relative;
}

.content h2 {
    color: #fff;
    font-size: 3rem;
    position: absolute;
    letter-spacing: 5px;
    font-weight: bolder;

}

.content h2:nth-child(1) {
    color: transparent;
    -webkit-text-stroke: 1px var(--ins-primary);
}

.content h2:nth-child(2) {
    background: -webkit-linear-gradient(45deg,var(--ins-secondary), var(--ins-primary));
    -webkit-text-fill-color: transparent;
    background-clip: text;
    /* color: var(--ins-primary); */
    animation: animate 3s ease-in-out infinite;
}

@keyframes animate {
    0%, 100% {
        clip-path: polygon(0% 45%,
                16% 44%,
                33% 58%,
                54% 60%,
                70% 61%,
                84% 59%,
                100% 52%,
                100% 100%,
                0% 100%);
    }

    50% {
        clip-path: polygon(0% 60%,
                15% 65%,
                34% 66%,
                51% 62%,
                67% 50%,
                84% 45%,
                100% 46%,
                100% 100%,
                0% 100%);
    }
}